@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: "Netflix";
  src: url("../public/assets/Netflix\ Sans\ Medium.otf");
}


.nav_black{
  background-color: #111;
}
.banner--fadeBottom{
  height: 0.4rem;
  background-image : linear-gradient( to right,
    360deg,transparent,rgba(37,37,37,0.61) 50%,
    rgba(0,0,0,0.4) 80%
  );
  box-shadow: 0 10px 20px -19px rgb(219, 28, 28);
}

.banner{
  position: relative;
  height: 550px;
  color: white;
  object-fit: contain;
  background-size : cover;
  background-position:center center;
  background-repeat:'no-repeat';
  /* background-image: linear-gradient(rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.5)); */
}
.row__posters{
  display: flex;
  overflow-y: hidden;
  overflow-x: auto;
  /* padding: 20px; */
  padding-top: 20px;
  /* border: 2px solid red; */
  cursor: grab;
}
.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster{
  max-height: 100px; 
  object-fit: contain;
  margin-right: 10px;
  transition: transform 450ms;
}
.row__poster:hover{
    transform: scale(1.08);
    opacity: 1;
}
.row__posterLarge{
  max-height: 250px;

}
