.youtube-video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.78) 30%, rgba(0, 0, 0, 0.3));
}.slick-prev {
  left: -50px; /* Adjust the left position to position the prev arrow */
}

.slick-next {
  right: -50px; /* Adjust the right position to position the next arrow */
}